.shortcode-modal-btn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 500;
}
.shortcode-modal-btn svg {
  width: 24px;
}

.shortcode-modal-wrapper {
  position: relative;
}

@media (min-width: 992px) {
  .modal-shortcodes .modal-lg {
    max-width: 1200px;
  }
}
#shortcodePreview {
  max-width: 310px;
}

.icon-shortcode::after {
  content: url(../Icons/shortcode-light.svg);
}

[data-bs-theme=dark] .icon-shortcode::after {
  content: url(../Icons/shortcode-dark.svg);
}